import axios from "axios";
import React, { createContext, useEffect, useState } from "react";
export const ApiConext = createContext();

const ApiContextProvider = ({ children }) => {
  const [words, setWords] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [trigger, setTrigger] = useState(true);
  const [longest, setLongest] = useState(6);
  const [time, setTime] = useState(0);
  const timer = 180000;
  const getFromServer = async () => {
    await axios
      .get("http://api.local-mumy.cloudns.ph:70")
      .then((res) => {
        setWords(res.data);
        
        setIsLoading(false);
      })
      .catch((err) => console.log(err));
  };
  const pickComb = async () => {
      await axios.get("https://d534-31-13-242-87.eu.ngrok.io/comb.php")
        .then(res =>{
          setWords(JSON.parse(res.data[0].combination));
          // const arr = JSON.parse(res.data[0].combination[0])
          // const longest = arr?.reduce((acc, curr) => {
          //   return curr.length >= acc.length ? curr : acc;
          // }, "");
          setLongest(longest)
          console.log(longest * timer);
          setIsLoading(false);
          console.log(JSON.parse(res.data[0].combination))
        })
        .catch((err) => console.log(err));
    }
  
  const nextComb = async () =>{
    await axios.get('http://api.local-mumy.cloudns.ph:70/comb')
      .then(res => {console.log(res); setTrigger(!trigger)})
      .catch(err => console.log(err))
  }
  useEffect(() => {
    const fetchData = async () => {
      // await getFromServer();
      await pickComb();
      
    };
    fetchData();
    const int = setInterval(() =>{
      fetchData();
      nextComb();
    },180000 * longest)
    return () => clearInterval(int);
  }, [trigger]);
  return (
    <ApiConext.Provider
      value={{
        words,
        isLoading,
        setIsLoading,
        trigger,
        setTrigger,
      }}
    >
      {children}
    </ApiConext.Provider>
  );
};

export default ApiContextProvider;
