
export const hideBtn = (text) =>{
    const dis = document.querySelector('.send');
    if(text.length > 100){
        dis.style.display = 'none'
    }
    if(text.length < 100){
        dis.style.display = 'block'
    }
}
export const scroll = (value) =>{
    let pick = document.querySelectorAll('.text-buld:last-child')
    if(value.length !== 0) pick[0].scrollIntoView({ behavior: "smooth", block: "end", inline: "nearest" });
}

export const prev = (text) =>{
    if (!text) return;
    if(text.length > 100) return;
}

